import noop from "../utils/noop";
import extend from "../utils/extend";

let instance = null;

class Device {

    #settings;
    #isMobile = false;
    #onMobileEvent;
    #onDesktopEvent;

    constructor(options) {
        if (instance) {
            return instance;
        }

        instance = this;

        this.#settings = extend(true, {}, Device.defaults, options);
        this.#onMobileEvent = () => {
            this.#isMobile = true;
            this.#removeEvents();
        };

        this.#onDesktopEvent = () => {
            this.#removeEvents();
        };

        document.addEventListener('touchstart', this.#onMobileEvent);
        document.addEventListener('mousemove', this.#onDesktopEvent);
        window.addEventListener('scroll', this.#onDesktopEvent);
    }

    #removeEvents() {
        document.removeEventListener('touchstart', this.#onMobileEvent);
        document.removeEventListener('mousemove', this.#onDesktopEvent);
        window.removeEventListener('scroll', this.#onDesktopEvent);

        this.#settings.onDeviceTypeKnown(this.#isMobile);

        return this;
    }

    isMobile() {
        return this.#isMobile;
    }

    isDesktop() {
        return !this.#isMobile;
    }

    static getInstance() {
        if (!instance) {
            new Device();
        }

        return instance;
    }
}

Device.defaults = {
    onDeviceTypeKnown: noop
};

export default Device;
