import Scroll from './Scroll';
import extend from '../utils/extend';
import { SplittedText, Tween, easings } from 'oneloop.js'

class AnimatedTitle {

    #settings;
    #title = null;
    #chars = [];
    #scroll = null;
    #splittedText;

    constructor(title, options) {
        
        this.#settings = extend(true, {}, AnimatedTitle.defaults, options)
        this.#title = title;

        if (document.fonts.check('1em "Source Serif 4"')) {
            this.#initialise();
        } else {
            document.fonts.ready.then(() => this.#initialise());
        }
    }

    #initialise() {
        this.#splittedText = new SplittedText(this.#title, {
            byLine: true,
            byChar: true
        });

        this.#chars = this.#title.getElementsByClassName('st-char');

        this.render(0);

        if (this.#settings.observeOnScroll) {
            this.#scroll = Scroll.getInstance();
            this.#scroll.observe(this.#title, {
                onVisible: (scrollInfos, pRTW) => {
                    this.render(Math.max(0, pRTW.y))
                },
            })
        } else if (this.#settings.showOnInitialise) {
            this.show();
        }

        this.#title.style.visibility = 'visible';
    }

    show(callback) {
        new Tween({
            duration: Math.max(this.#chars.length * 142, 1000),
            onUpdate: (timestamp, tick, percent) => {
                this.render(percent)
            },
            onComplete: callback 
        })

        return this;
    }

    render(percent) {
        const chars = this.#chars;
        const length = chars.length;

        for (let i = 0; i < length; i++) {
            const p = easings.easeOutExpo(Math.max(Math.min(percent * 2 - (i / (length - 1)) * 0.5, 1), 0));

            chars[i].style.opacity = p;
            chars[i].style.transform = 'translateY(' + (24 - 24 * p) + '%) rotateX(' + (-90 + 90 * p) + 'deg)';
        }

        return this;
    }
}

AnimatedTitle.defaults = {
    observeOnScroll: true,
    showOnInitialise: true
};

export default AnimatedTitle
