import AnimatedTitle from './AnimatedTitle';
import PageDefault from './PageDefault';
import Scroll from './Scroll';
import { Tween, easings } from 'oneloop.js';

class PageWebsite extends PageDefault {

    #scroll;
    #work;
    #chapeau;
    #h1;
    #description;
    #btn;
    #container;

    constructor(options) {
        super(options);

        this.#scroll = Scroll.getInstance();

        this.#h1 = new AnimatedTitle(this.find('h1'), {
            observeOnScroll: false,
            showOnInitialise: false
        });

        this.#work = this.find('.work');
        this.#chapeau = this.find('.work__chapeau');
        this.#description = this.find('.is-style-large');
        this.#btn = this.find('.work__btn');
        this.#container = this.find('.container');

        this.#reset();
    }

    #initialise() {
        this.#scroll.observe('.work__devices', {
            onVisible: function(scrollInfos, pRTW, pRTE) {
                this.element.style.setProperty('--percent-y', pRTW.y);
            }
        });

        new Tween({
            duration: 250,
            onUpdate: (timestamp, tick, percent) => {
                this.#chapeau.style.opacity = percent;
            }
        });

        this.#h1.show();

        new Tween({
            delay: 500,
            duration: 2000,
            onUpdate: (timestamp, tick, percent) => {
                this.#description.style.opacity = percent;
                this.#description.style.transform = `translateY(${100 * (1 - easings.easeOutExpo(percent))}px)`;
            }
        });

        if (this.#btn) {
            new Tween({
                delay: 750,
                duration: 1750,
                onUpdate: (timestamp, tick, percent) => {
                    this.#btn.style.opacity = percent;
                    this.#btn.style.transform = `translateY(${125 * (1 - easings.easeOutExpo(percent))}px)`;
                }
            });
        }

        new Tween({
            delay: 1000,
            duration: 1500,
            onUpdate: (timestamp, tick, percent) => {
                this.#container.style.opacity = percent;
                this.#container.style.transform = `translateY(${150 * (1 - easings.easeOutExpo(percent))}px)`;
            }
        });

        return this;
    }

    #reset() {
        this.#chapeau.style.opacity = 0;
        this.#h1.render(0);
        this.#description.style.opacity = 0;
        this.#description.style.transform = 'translateY(100px)';
        this.#container.style.opacity = 0;
        this.#container.style.transform = 'translateY(150px)';

        if (this.#btn) {
            this.#btn.style.opacity = 0;
            this.#btn.style.transform = 'translateY(125px)';
        }
        
        return this;
    }

    afterIntro() {
        this.#initialise();
        return this;
    }

    beforeSwap(data, callback) {
        new Tween({
            duration: 1000,
            onUpdate: (timestamp, tick, percent) => {
                this.#work.style.opacity = 1 - percent;
                this.#work.style.transform = `translateY(${-200 * easings.easeInExpo(percent)}px)`;
            },
            onComplete: () => {
                this.#reset();

                this.#work.style.opacity = '';
                this.#work.style.transform = '';

                if (typeof callback === 'function') {
                    callback();
                }
            }
        });

        return this;
    }

    afterSwap(data, callback) {
        this.setContentVisible();
        this.#initialise();
       
        if (typeof callback === 'function') {
            callback();
        }

        return this;
    }
}

export default PageWebsite;
